import { diasDiff } from "./diasDif";

const fechaDiffAniversario = (fechaOrigen) => {
  const dias = diasDiff(fechaOrigen);

  if (dias < 0) {
    return {
      dias,
      msg: `Ya celebraron su aniversario hace ${dias * -1} das`,
    };
  } else if (dias === 0) {
    return {
      dias,
      msg: "Hoy es su aniversario",
    };
  } else {
    return {
      dias,
      msg: `Faltan ${dias} das para su aniversario`,
    };
  }
};

